/* MonsterDisplay.module.css */
:root {
  --bg-dark: #1a1a2e;
  --bg-medium: #16213e;
  --bg-light: #252941;
  --accent-1: #7209b7;
  --accent-2: #4361ee;
  --accent-3: #4cc9f0;
  --text-light: #f8f9fa;
  --text-medium: #b8b8b8;
  --danger: #ff5151;
  --success: #39e75f;
}

.container {
  background-color: var(--bg-medium);
  color: var(--text-light);
  padding: 1.5rem;
  border-radius: 10px;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
  margin-bottom: 1rem;
  border: 1px solid rgba(114, 9, 183, 0.3);
  position: relative;
  overflow: hidden;
}

.container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 5px;
  background: linear-gradient(90deg, var(--accent-1), var(--accent-2), var(--accent-3));
}

.name {
  color: var(--accent-3);
  font-size: 2rem;
  margin-top: 0.5rem;
  margin-bottom: 1rem;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 2px;
  text-shadow: 0 0 8px rgba(76, 201, 240, 0.5);
}

.stat {
  background-color: var(--bg-dark);
  padding: 0.75rem 1rem;
  margin: 0.5rem 0;
  border-radius: 6px;
  font-size: 1rem;
  display: flex;
  justify-content: space-between;
  position: relative;
  transition: all 0.2s ease;
}

.stat::before {
  content: '';
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 4px;
  background-color: var(--accent-2);
  border-radius: 4px 0 0 4px;
}

.stat:hover {
  transform: translateX(5px);
  box-shadow: 0 2px 8px rgba(67, 97, 238, 0.3);
}

.noData {
  background-color: var(--bg-medium);
  color: var(--text-medium);
  padding: 2rem;
  text-align: center;
  border-radius: 10px;
  font-size: 1.2rem;
  box-shadow: 0 5px 15px rgba(0, 0, 0, 0.4);
}

/* Base button style - retained from your original */
.button {
  background: linear-gradient(135deg, var(--accent-1), var(--accent-2));
  color: white;
  border: none;
  padding: 1rem 2rem;
  width: 100%;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  box-shadow: 0 4px 12px rgba(67, 97, 238, 0.4);
  transition: all 0.3s ease;
  margin-top: 0.5rem;
}

/* Green button style - for when monster is NOT in team */
.buttonGreen {
  background: linear-gradient(135deg, #2a9d8f, var(--success));
  color: white;
  border: none;
  padding: 1rem 2rem;
  width: 100%;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  box-shadow: 0 4px 12px rgba(57, 231, 95, 0.4);
  transition: all 0.3s ease;
  margin-top: 1.5rem;
}

.buttonGreen:hover {
  background: linear-gradient(135deg, #268378, #32d054);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(57, 231, 95, 0.6);
}

.buttonGreen:active {
  transform: translateY(1px);
}

/* Blue button style - for when monster IS in team */
.buttonBlue {
  background: linear-gradient(135deg, #3a86ff, var(--accent-3));
  color: white;
  border: none;
  padding: 1rem 2rem;
  width: 100%;
  border-radius: 6px;
  cursor: pointer;
  font-size: 1.2rem;
  font-weight: bold;
  letter-spacing: 1px;
  text-transform: uppercase;
  box-shadow: 0 4px 12px rgba(76, 201, 240, 0.4);
  transition: all 0.3s ease;
  margin-top: 1.5rem;
}

.buttonBlue:hover {
  background: linear-gradient(135deg, #2878f0, #41b7dc);
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(76, 201, 240, 0.6);
}

.buttonBlue:active {
  transform: translateY(1px);
}

