/* New styles for the image container and image */
.imageContainer {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 1rem 0;
  max-height: 200px;
  overflow: hidden;
  border-radius: 8px;
  background-color: var(--bg-dark);
  padding: 0.5rem;
}

.monsterImage {
  max-width: 100%;
  max-height: 180px;
  object-fit: contain;
  border-radius: 6px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.noImage {
  color: var(--text-medium);
  font-style: italic;
  padding: 2rem;
  text-align: center;
}

/* Success message style */
.successMessage {
  color: var(--success);
  text-align: center;
  margin-top: 1rem;
  font-weight: bold;
  animation: fadeIn 0.3s ease-in;
}

@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 1; }
}

/* Style for the JSON preview */
pre.stat {
  font-family: 'Courier New', monospace;
  font-size: 0.9rem;
  white-space: pre-wrap;
  overflow-x: auto;
  display: block;
  text-align: left;
  padding: 1rem;
  margin-top: 1.5rem;
  border-top: 1px dashed rgba(76, 201, 240, 0.3);
  background-color: rgba(26, 26, 46, 0.7);
}

@media (max-width: 768px) {
  .container {
    padding: 1rem;
  }
  
  .name {
    font-size: 1.5rem;
  }
  
  .stat {
    font-size: 0.9rem;
    padding: 0.5rem 0.75rem;
  }
  
  .buttonGreen, .buttonBlue, .button {
    padding: 0.75rem 1.5rem;
    font-size: 1rem;
  }
}