/* CreateMonster.module.css */
:root {
    --bg-dark: #1a1a2e;
    --bg-medium: #16213e;
    --bg-light: #252941;
    --accent-1: #7209b7;
    --accent-2: #4361ee;
    --accent-3: #4cc9f0;
    --text-light: #f8f9fa;
    --text-medium: #b8b8b8;
    --danger: #ff5151;
    --success: #39e75f;
  }
  
  .container {
    background-color: var(--bg-dark);
    color: var(--text-light);
    max-width: 700px;
    margin: 2rem auto;
    padding: 2rem;
    border-radius: 12px;
    box-shadow: 0 8px 32px rgba(0, 0, 0, 0.5);
    border: 1px solid rgba(114, 9, 183, 0.3);
  }
  
  .title {
    color: var(--accent-3);
    text-align: center;
    font-size: 2.5rem;
    margin-bottom: 1.5rem;
    text-shadow: 0 0 10px var(--accent-3);
    letter-spacing: 2px;
  }
  
  .form {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
  }
  
  .formGroup {
    display: flex;
    flex-direction: column;
  }
  
  .label {
    font-weight: bold;
    margin-bottom: 0.5rem;
    color: var(--accent-2);
  }
  
  .input {
    background-color: var(--bg-light);
    color: var(--text-light);
    border: 2px solid rgba(67, 97, 238, 0.3);
    padding: 0.75rem;
    border-radius: 6px;
    font-size: 1rem;
    transition: all 0.3s ease;
  }
  
  .input:focus {
    outline: none;
    border-color: var(--accent-2);
    box-shadow: 0 0 8px var(--accent-2);
  }
  
  .button {
    grid-column: span 2;
    margin-top: 1rem;
    background: linear-gradient(135deg, var(--accent-1), var(--accent-2));
    color: white;
    border: none;
    padding: 1rem;
    border-radius: 6px;
    cursor: pointer;
    font-size: 1.2rem;
    font-weight: bold;
    letter-spacing: 1px;
    text-transform: uppercase;
    box-shadow: 0 4px 12px rgba(67, 97, 238, 0.4);
    transition: all 0.3s ease;
  }
  
  .button:hover {
    background: linear-gradient(135deg, var(--accent-1), var(--accent-3));
    transform: translateY(-2px);
    box-shadow: 0 6px 16px rgba(67, 97, 238, 0.6);
  }
  
  .button:active {
    transform: translateY(1px);
  }
  
  .previewContainer {
    background-color: var(--bg-medium);
    padding: 1rem;
    border-radius: 8px;
    margin-bottom: 2rem;
    border-left: 4px solid var(--accent-1);
  }
  
  .previewTitle {
    color: var(--accent-3);
    margin-top: 0;
    margin-bottom: 0.5rem;
  }
  
  .preview {
    background-color: var(--bg-light);
    padding: 1rem;
    border-radius: 6px;
    color: var(--text-medium);
    font-family: 'Courier New', monospace;
    font-size: 0.9rem;
    overflow-x: auto;
    white-space: pre-wrap;
  }
  
  /* Media query for smaller screens */
  @media (max-width: 768px) {
    .form {
      grid-template-columns: 1fr;
    }
  
    .button {
      grid-column: span 1;
    }
  }

